export function highlightAnnotationsInSamples (samples) {
  for (let sample of samples) {
    sample.highlightedText = sample.text
    let annotations = filterAnnotationsForRequiredAttributes(sample.getAnnotations())
    annotations = orderArrayOfObjectsByPropertyDESC(annotations, 'begin')
    for (let annotation of annotations) sample.highlightedText = highlightWithOffset(annotation, sample)
  }
  return samples
}

function orderArrayOfObjectsByPropertyDESC (array, property) {
  return array.sort((object1, object2) => { return object2[property] - object1[property] })
}

function highlightWithOffset (annotation, sample) {
  let offset = sample.begin
  let start = sample.highlightedText.slice(0, annotation.begin - offset)
  let highlight = sample.highlightedText.slice(annotation.begin - offset, annotation.begin + annotation.length - offset)
  let end = sample.highlightedText.slice(annotation.begin + annotation.length - offset)

  if (annotation.type !== 'DELETED') highlight = surroundWithSpanAndAddCSSClass(annotation.source, highlight)

  return start.concat(highlight, end)
}

function surroundWithSpanAndAddCSSClass (type, text) {
  let cssClass = ''.concat('class="highlight-', type, '"')
  return ''.concat('<span ', cssClass, '>', text, '</span>')
}

function filterAnnotationsForRequiredAttributes (annotations) {
  return annotations.filter(annotation => annotation.begin && annotation.length && annotation.text)
}



