import Vue from 'vue'
import Vuex from 'vuex'

import * as API from '@/api/trainer-api'
import * as highlight from '@/text-interactions/highlight-text'
import * as texoo from '@/api/texoo-datamodel'
import * as parser from '@/api/parse-response'

Vue.use(Vuex)

