<template>
  <v-toolbar app dark color="primary" clipped-left flat ref="search">
    <v-tooltip bottom>
      <v-toolbar-title slot="activator"><router-link :to="{ name: 'LandingPage' }" tag="span" :style="{ cursor: 'pointer' }">FashionBrain Relation Extractor</router-link></v-toolbar-title>
      <span>open user guide</span>
    </v-tooltip>
    <v-spacer></v-spacer>
    <v-toolbar-items>
      <v-tooltip bottom><v-btn slot="activator" flat :to="{ name: 'Resources', params: { resource: 'samples' }}"><v-icon left>list</v-icon>Samples</v-btn><span>request samples</span></v-tooltip>
      <v-tooltip bottom><v-btn slot="activator" flat v-on:click="reOpenClosedSamples"><v-icon>settings_backup_restore</v-icon></v-btn><span>reopen closed samples</span></v-tooltip>
      <v-tooltip bottom><v-btn slot="activator" flat :to="{ name: 'UploadSamples' }" v-on:click='saveState'><v-icon>save</v-icon></v-btn><span>upload processed samples</span></v-tooltip>
    </v-toolbar-items>
  </v-toolbar>
</template>

<script>
export default {
  name: 'Toolbar',

  methods: {
    toggleSidebar () {
      this.$store.commit('toggleSidebar')
    },
    reOpenClosedSamples () {
      this.$store.commit('reOpenClosedSamples')
    },
    saveState () {
      this.$store.dispatch('saveState')
    }
  },

  computed: {
    showSidebar () {
      return this.$store.state.showSidebar
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



