<template>
  <v-container class="flex-container">
    <!-- <Pagination></Pagination> -->
    <Dropdown/>

    <div class="card-container">
      <Card
        v-for="sample in content"
        v-bind:key="sample.uid"
        v-bind:sample="sample">
      </Card>
    </div>

    <!-- <Pagination></Pagination> -->
  </v-container>
</template>

<script>
import Pagination from '@/components/Pagination.vue'
import Dropdown from '@/components/Dropdown.vue'
import Card from '@/components/cards/Card.vue'

export default {
  name: 'Samples',

  components: {
    Card,
    Pagination,
    Dropdown
  },

  data () {
    return {
      resourceToDisplay: this.$route.params.resource
    }
  },

  mounted () {
    this.request(this.resourceToDisplay, this.$route.params.id)
  },

  methods: {
    request (resource, pageNumber) {
      if (resource === 'samples' && this.arrayIsEmpty(this.samples)) this.$store.dispatch('getSamples', this.pageNumber)
    },

    arrayIsEmpty (array) {
      return array.length === 0
    }
  },

  computed: {
    pageNumber () {
      return this.$store.state.pageNumber
    },

    autocompleteSelectedToken () {
      return this.$store.state.autocompleteSelectedToken
    },

    samples () {
      return this.$store.state.samples
    },

    documents () {
      return this.$store.state.documents
    },

    sessions () {
      return this.$store.state.sessions
    },

    content () {
      if (this.resourceToDisplay === 'samples') return this.samples
      else if (this.resourceToDisplay === 'documents') return this.documents
    }
  }
}
</script>

<style scoped>
  .flex-container {
    max-width: 1000px;
    min-width: 500px;
  }

  .card-container {
    display: flex;
    flex-direction: column;

  }
</style>



