<template>
  <div>
     <div reverse class="flex-container elevation-5 margin-between-cards">
       <div><v-tooltip bottom><v-btn v-on:click="decreasePageNumber" flat slot="activator"><v-icon>chevron_left</v-icon></v-btn><span>previous samples</span></v-tooltip></div>
       <div class="pageNumber">{{pageNumber}}</div>
       <div><v-tooltip bottom><v-btn v-on:click="increasePageNumber" flat slot="activator"><v-icon>chevron_right</v-icon></v-btn><span>next samples</span></v-tooltip></div>
     </div>
   </div>
</template>

<script>
export default {
  name: 'Pagination',

  methods: {
    decreasePageNumber () {
      if (this.pageNumber > 0) {
        this.$store.commit('setPageNumber', this.pageNumber - 1)
        this.$store.dispatch('updateState')
        this.$store.dispatch('getSamples', this.pageNumber)
      }
    },

    increasePageNumber () {
      this.$store.commit('setPageNumber', this.pageNumber + 1)
      this.$store.dispatch('updateState')
      this.$store.dispatch('getSamples', this.pageNumber)
    }
  },

  computed: {
    pageNumber () {
      return this.$store.state.pageNumber
    }
  }
}
</script>

<style scoped lang="scss">
  .margin-between-cards {
    margin-bottom: 12px;
  }

  .flex-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .pageNumber {
    font-size: 12pt;
  }
</style>



