<template>
  <div>
     <div reverse class="flex-container elevation-5 margin-between-cards">
       <v-overflow-btn
          class="my-2"
          :items="dropdown_icon"
          label="ID: lnsNTaw0z1li7n5WiggR"
          segmented
          target="#dropdown-example"
        ></v-overflow-btn>
     </div>
   </div>
</template>

<script>
export default {
  name: 'Pagination',

  data () {
    return {
      dropdown_icon: []
    }
  },

  methods: {
    decreasePageNumber () {
      if (this.pageNumber > 0) {
        this.$store.commit('setPageNumber', this.pageNumber - 1)
        this.$store.dispatch('updateState')
        this.$store.dispatch('getSamples', this.pageNumber)
      }
    },

    increasePageNumber () {
      this.$store.commit('setPageNumber', this.pageNumber + 1)
      this.$store.dispatch('updateState')
      this.$store.dispatch('getSamples', this.pageNumber)
    }
  },

  computed: {
    pageNumber () {
      return this.$store.state.pageNumber
    }
  }
}
</script>

<style scoped lang="scss">
  .margin-between-cards {
    margin-bottom: 12px;
  }

  .flex-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .pageNumber {
    font-size: 12pt;
  }
</style>



