<template>
  <v-navigation-drawer :value="showSidebar" clipped stateless app>
    <canvas id="f1Score"></canvas>
    <canvas id="finishedSamples"></canvas>
  </v-navigation-drawer>
</template>

<script>
  import Chart from 'chart.js'

  export default {
    name: 'Sidebar',

    data () {
      return {
        f1ScoreChart: {},
        finishedSamplesChart: {},
        countSamples: 0,
        countAnnotations: 0,
        countfinishedSamples: 0,
        countfinishedAnnotations: 0
      }
    },

    mounted () {
      this.f1ScoreChart = this.createChart('f1Score', this.createF1ScoreChartLayout())
      this.finishedSamplesChart = this.createChart('finishedSamples', this.createFinishedSamplesChartLayout())
    },

    methods: {
      createChart (canvasId, chartData) {
        const context = document.getElementById(canvasId)
        return new Chart(context, {
          type: chartData.type,
          data: chartData.data,
          options: chartData.options
        })
      },

      createF1ScoreChartLayout () {
        return {
          type: 'line',
          data: {
            labels: [''], // removing the labels
            datasets: [
              {
                label: 'F1-Score',
                data: [],
                borderWidth: 2,
                borderColor: '#90CAF9',
                fill: false
              }
            ]
          },
          options: {
            title: {
              display: true,
              text: 'F1-Score'
            },
            responsive: true,
            legend: { display: false },
            lineTension: 0,
            scales: {
              yAxes: [{
                ticks: {
                  beginAtZero: true,
                  min: 0,
                  max: 1,
                  padding: 5
                }
              }]
            }
          }
        }
      },

      updateF1Score () {
        this.f1ScoreChart.data.labels = []
        this.f1ScoreChart.data.datasets[0].data = []
        for (let score of this.sessions[0].f1Score) {
          this.f1ScoreChart.data.labels.push('')
          this.f1ScoreChart.data.datasets[0].data.push(score)
        }
        this.f1ScoreChart.update()
      },

      createFinishedSamplesChartLayout () {
        return {
          type: 'horizontalBar',
          data: {
            labels: ['Samples', 'Annotations'],
            datasets: [
              {
                label: '',
                data: [],
                // backgroundColor: "#90CAF9",
                backgroundColor: ['#90CAF9', '#EF9A9A']
              }
            ]
          },
          options: {
            title: {
              display: true,
              text: 'Finished Samples and Annotations [%]'
            },
            responsive: true,
            legend: { display: false },
            lineTension: 0,
            scales: {
              xAxes: [{
                ticks: {
                  beginAtZero: true,
                  min: 0,
                  max: 100
                }
              }],
              yAxes: [{
                ticks: {
                  // padding: 22
                }
              }]
            }
          }
        }
      },

      updateFinishedSamplesChart () {
        let percentageOfFinishedSamples = Math.round((this.countfinishedSamples / this.countSamples) * 100)
        let percentageOfFinishedAnnotations = Math.round((this.countfinishedAnnotations / this.countAnnotations) * 100)
        this.finishedSamplesChart.data.datasets[0].data = [percentageOfFinishedSamples, percentageOfFinishedAnnotations]
        this.finishedSamplesChart.update()
      },

      countFinishedSamplesAndAnnotations () {
        this.countSamples = this.samples.length
        this.countfinishedSamples = 0
        this.countAnnotations = 0
        this.countfinishedAnnotations = 0

        for (let sample of this.samples) {
          if (sample.finished === true) this.countfinishedSamples++
          for (let annotation of sample.annotations) {
            if (annotation.type !== 'DELETED') {
              this.countAnnotations++
              if (sample.finished === true) this.countfinishedAnnotations++
            }
          }
        }
      }
    },

    computed: {
      showSidebar () {
        return this.$store.state.showSidebar
      },

      sessions () {
        return this.$store.state.sessions
      },

      samples () {
        return this.$store.state.samples
      }
    },

    watch: {
      samples: {
        handler: function (val, oldVal) {
          this.countFinishedSamplesAndAnnotations()
          this.updateFinishedSamplesChart()
        },
        deep: true
      },

      sessions: function () {
        this.updateF1Score()
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



