export class Session {
  constructor (uid, f1Score) {
    this._uid = uid
    this._f1Score = f1Score
  }

  get uid () { return this._uid }
  get f1Score () { return this._f1Score }

  set uid (uid) { this._uid = uid }
  set f1Score (f1Score) { this._f1Score = f1Score }
}



