export class Span {
  constructor (length, documentRef, uid, text, begin, classs, type) {
    this._length = length
    this._documentRef = documentRef
    this._uid = uid
    this._text = text
    this._begin = begin
    this._classs = classs
    this._type = type
  }

  get length () { return this._length }
  get documentRef () { return this._documentRef }
  get uid () { return this._uid }
  get text () { return this._text }
  get begin () { return this._begin }
  get classs () { return this._classs }
  get type () { return this._type }

  set length (length) { this._length = length }
  set documentRef (documentRef) { this._documentRef = documentRef }
  set uid (uid) { this._uid = uid }
  set text (text) { this._text = text }
  set begin (begin) { this._begin = begin }
  set classs (classs) { this._classs = classs }
  set type (type) { this._type = type }

  toString () {
    let str = ''

    for (let key in this) {
      let keyWithoutUnderscore = key.replace('_', '')
      let value = this[keyWithoutUnderscore] // to invoke the getter
      str += '- ' + keyWithoutUnderscore + ': ' + value + '\n'
    }

    return str
  }

  toJSON () {
    let object = {}

    for (let key in this) {
      let keyWithoutUnderscore = key.replace('_', '')
      let value = this[keyWithoutUnderscore] // to invoke the getter
      object[keyWithoutUnderscore] = value
    }

    return object
  }
}

export class Sample extends Span {
  constructor (length, documentRef, uid, text, begin, classs, type, tokens, empty, annotations, language, sentences, source, id, finished, highlightedText, title) {
    super(length, documentRef, uid, text, begin, classs, type)
    this._tokens = tokens
    this._empty = empty
    this._annotations = annotations
    this._language = language
    this._sentences = sentences
    this._source = source
    this._id = id
    this._finished = finished
    this._highlightedText = highlightedText
    this._title = title
  }

  get tokens () { return this._tokens }
  get empty () { return this._empty }
  get annotations () { return this._annotations }
  get language () { return this._language }
  get sentences () { return this._sentences }
  get source () { return this._source }
  get id () { return this._id }
  get finished () { return this._finished }
  get highlightedText () { return this._highlightedText }
  get title () { return this._title }

  set tokens (tokens) { this._tokens = tokens }
  set empty (empty) { this._empty = empty }
  set annotations (annotations) { this._annotations = annotations }
  set language (language) { this._language = language }
  set sentences (sentences) { this._sentences = sentences }
  set source (source) { this._source = source }
  set id (id) { this._id = id }
  set finished (finished) { this._finished = finished }
  set highlightedText (highlightedText) { this._highlightedText = highlightedText }
  set title (title) { this._highlightedText = title }

  getRelations () {
    return this._annotations.filter(annotation => annotation.classs === 'relationAnnotation')
  }

  getAnnotations () {
    const annotations = this._annotations.filter(annotation => annotation.classs !== 'relationAnnotation')
    const relations = this._annotations.filter(annotation => annotation.classs === 'relationAnnotation')
    for (let relation of relations) annotations.push(...relation.relationArguments)
    return annotations
  }
}

export class Article extends Span {
  constructor (length, documentRef, uid, text, begin, classs, description, type, title, url, terms, vector, names, id) {
    super(length, documentRef, uid, text, begin, classs, type)
    this._description = description
    this._title = title
    this._url = url
    this._terms = terms
    this._vector = vector
    this._names = names
    this._id = id
  }

  get description () { return this._description }
  get title () { return this._title }
  get url () { return this._url }
  get terms () { return this._terms }
  get vector () { return this._vector }
  get names () { return this._names }
  get id () { return this._id }

  set description (description) { this._description = description }
  set title (title) { this._title = title }
  set url (url) { this._url = url }
  set terms (terms) { this._terms = terms }
  set vector (vector) { this._vector = vector }
  set names (names) { this._names = names }
  set id (id) { this._id = id }
}

export class Annotation extends Span {
  constructor (length, documentRef, uid, text, begin, classs, type, source, confidence, isActive) {
    super(length, documentRef, uid, text, begin, classs, type)
    this._source = source
    this._confidence = confidence
    this._isActive = isActive
  }

  get source () { return this._source }
  get confidence () { return this._confidence }
  get isActive () { return this._isActive }

  set source (source) { this._source = source }
  set confidence (confidence) { this._confidence = confidence }
  set isActive (isActive) { this._isActive = isActive }
}

export class Relation extends Annotation {
  constructor (length, documentRef, uid, text, begin, classs, type, source, confidence, isActive, predicate, relationArguments) {
    super(length, documentRef, uid, text, begin, classs, type, source, confidence, isActive)
    this._predicate = predicate
    this._relationArguments = relationArguments
  }

  get predicate () { return this._predicate }
  get relationArguments () { return this._relationArguments }

  set predicate (predicate) { this._predicate = predicate }
  set relationArguments (relationArguments) { this._relationArguments = relationArguments }
}

export class Context extends Span {
  constructor (length, documentRef, uid, text, begin, classs, language, cursor, timestamp, type) {
    super(length, documentRef, uid, text, begin, classs, type)
    this._language = language
    this._cursor = cursor
    this._timestamp = timestamp
  }

  get language () { return this._language }
  get cursor () { return this._cursor }
  get timestamp () { return this._timestamp }

  set language (language) { this._language = language }
  set cursor (cursor) { this._cursor = cursor }
  set timestamp (timestamp) { this._timestamp = timestamp }
}

export class Sentence extends Span {
  constructor (length, documentRef, uid, text, begin, classs, tokens, empty, type) {
    super(length, documentRef, uid, text, begin, classs, type)
    this._tokens = tokens
    this._empty = empty
  }

  get tokens () { return this._tokens }
  get empty () { return this._empty }

  set tokens (tokens) { this._tokens = tokens }
  set empty (empty) { this._empty = empty }
}

export class Token extends Span {
  constructor (length, documentRef, uid, text, begin, classs, empty, type) {
    super(length, documentRef, uid, text, begin, classs, type)
    this._empty = empty
  }

  get empty () { return this.empty }

  set empty (empty) { this._empty = empty }
}

export class Dataset {
  constructor (name, randomDocument, language, uid, documents, randomSentence) {
    this._name = name
    this._randomDocument = randomDocument
    this._language = language
    this._uid = uid
    this._documents = documents
    this._randomSentence = randomSentence
  }

  get name () { return this._name }
  get randomDocument () { return this._randomDocument }
  get language () { return this._language }
  get uid () { return this._uid }
  get documents () { return this._documents }
  get randomSentence () { return this._randomSentence }

  set name (name) { this._name = name }
  set randomDocument (randomDocument) { this._randomDocument = randomDocument }
  set language (language) { this._language = language }
  set uid (uid) { this._uid = uid }
  set documents (documents) { this._documents = documents }
  set randomSentence (randomSentence) { this._randomSentence = randomSentence }
}



