<template>
<v-container>
  <v-layout>
    <v-flex md9 offset-md2>
      <h1>TraiNER - User Guide</h1>
      <h2>The Interface</h2>
      <hr />
      <p>Below screenshot presents the TraiNER interface, the provided interactions are numbered and described in detail in the table below.</p>
      <img src="@/assets/trainer-frontend.png" width="800">

      <v-data-table :headers="guiHeaders" :items="guiData" hide-actions class="elevation-1">
        <template slot="items" slot-scope="props">
          <td>{{ props.item.number }}</td>
          <td>{{ props.item.description }}</td>
        </template>
      </v-data-table>

      <h2>Annotating</h2>
      <hr />
      <p>Below table contains the operations that can be performed on annotations</p>

      <v-data-table :headers="interactionHeaders" :items="interactionData" hide-actions class="elevation-1">
        <template slot="items" slot-scope="props">
          <td>{{ props.item.issue }}</td>
          <td>{{ props.item.action }}</td>
        </template>
      </v-data-table>
    </v-flex>
  </v-layout>
</v-container>
</template>

<script>
export default {
  name: 'LandingPage',

  data () {
    return {
      guiHeaders: [{
        text: 'Number',
        align: 'left',
        sortable: false,
        value: 'number'
      },
      {
        text: 'Description',
        align: 'left',
        sortable: false,
        value: 'description'
      }],
      guiData: [{
        number: 1,
        description: 'Toggle sidebar to investigate F1-score and progress'
      },
      {
        number: 2,
        description: 'Get Instructions on how to use the interface'
      },
      {
        number: 3,
        description: 'Request documents'
      },
      {
        number: 4,
        description: 'Request samples'
      },
      {
        number: 5,
        description: 'Reopen closed samples'
      },
      {
        number: 6,
        description: 'Upload processed samples'
      },
      {
        number: 7,
        description: 'The F1-score'
      },
      {
        number: 8,
        description: 'The progress'
      },
      {
        number: 9,
        description: 'Toggle the autocompletion of tokens'
      },
      {
        number: 10,
        description: 'A sample'
      },
      {
        number: 11,
        description: 'A annotation by the user'
      },
      {
        number: 12,
        description: 'A annotation from TraiNER'
      },
      {
        number: 13,
        description: 'Get the document related to the sample'
      },
      {
        number: 14,
        description: 'Mark this sample as finished'
      },
      {
        number: 15,
        description: 'Remove all annotations from this sample'
      }],
      interactionHeaders: [{
        text: 'Issue',
        align: 'left',
        sortable: false,
        value: 'issue'
      },
      {
        text: 'Action',
        align: 'left',
        sortable: false,
        value: 'action'
      }],
      interactionData: [{
        issue: 'Add annotation',
        action: 'Mark tokens, e.g. by double clicking'
      },
      {
        issue: 'Delete annotation',
        action: 'Click at the annotation'
      },
      {
        issue: 'Shrink annotation',
        action: 'Delete annotation and re-add shorter annotation'
      },
      {
        issue: 'Expand annotation',
        action: 'Delete annotation and re-add wider annotation'
      }]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h2 {
  margin-top: 10px;
}

p {
  margin-top: 10px;
}

img {
  margin-bottom: 10px;
}
</style>



