<template>
  <v-app>
    <Toolbar/>
    <v-content>
      <router-view
        :key="$route.fullPath">
      </router-view>
    </v-content>
  </v-app>
</template>

<script>
import Toolbar from './components/Toolbar.vue'
import Sidebar from './components/Sidebar.vue'

export default {
  name: 'App',

  components: {
    Sidebar,
    Toolbar
  }
}
</script>

<style lang="scss">
  @import 'sass-material-design-colors/palette.scss';
  .ACTIVE {
    background-color: $blue-200 !important;
  }

  .highlight-GOLD {
    background-color: $yellow-200;
  }

  .highlight-USER {
    background-color: $green-200;
  }

  .highlight-SILVER {
    background-color: $blue-200;
  }
  .highlight-PRED {}
  .highlight-SAMPLED {}
  .highlight-TRAIN {}
</style>



