<template>
  <div class="position-wrapper">
    <!-- <div class="card-text transparent stacked"><pre v-on:click="userMarksText($event)" v-html="sample.text"></pre></div> -->
    <div class="card-text"><pre v-html="sample.highlightedText"></pre></div>
  </div>
</template>

<script>
export default {
  name: 'MarkText',

  props: { sample: this.sample },

  methods: {
    userMarksText (event) {
      let payload = { event: event, selection: this.getSelection() }
      this.$emit('markText', payload)
    },

    getSelection () {
      let offset
      let length
      let selectedText

      if (typeof window.getSelection !== 'undefined') {
        let selection = window.getSelection()
        let selectionBegin = selection.anchorOffset
        let selectionEnd = selection.focusOffset
        offset = this.markedFromLeftToRight(selectionBegin, selectionEnd) ? selectionBegin : selectionEnd
        selectedText = selection.toString()
        length = selectedText.length

        let range = selection.getRangeAt(0)
        let surroundingTag = range.startContainer.parentNode.tagName
        let previousSibling = this.isSpan(surroundingTag) ? range.startContainer.parentNode.previousSibling : range.startContainer.previousSibling

        while (this.hasPreviousSibling(previousSibling)) {
          let textInPreviousSibling = this.isSpan(previousSibling.tagName) ? previousSibling.innerText : previousSibling.data
          offset += textInPreviousSibling.length
          previousSibling = previousSibling.previousSibling
        }

        return {begin: offset, length: length, text: selectedText}
      }
    },

    markedFromLeftToRight (start, end) {
      return start < end
    },

    isSpan (tag) {
      return tag === 'SPAN'
    },

    hasPreviousSibling (node) {
      return node !== null
    }
  }
}
</script>

<style scoped lang="scss">
  pre {
    white-space: pre-wrap;       /* Since CSS 2.1 */
    white-space: -moz-pre-wrap;  /* Mozilla, since 1999 */
    white-space: -pre-wrap;      /* Opera 4-6 */
    white-space: -o-pre-wrap;    /* Opera 7 */
    word-wrap: break-word;       /* Internet Explorer 5.5+ */

    font-family: roboto, sans-serif;
    font-size: 12pt;
  }

  .card-text {
    padding-top: 0px;
  }

  .unselectable {
    -webkit-touch-callout: none; /* iOS Safari */
      -webkit-user-select: none; /* Safari */
       -khtml-user-select: none; /* Konqueror HTML */
         -moz-user-select: none; /* Firefox */
          -ms-user-select: none; /* Internet Explorer/Edge */
              user-select: none; /* Non-prefixed version, currently
                                    supported by Chrome and Opera */
  }

  .stacked {
    position: absolute;
  }

  .position-wrapper {
    position: relative;
  }

  .transparent {
    opacity: 0.8;
  }
</style>



