export function getSample (uid) {
  return get('samples/' + uid)
    // eslint-disable-next-line
    .then(response => parser.instantiateSamples(response))
}

export function getDocuments (pageNumber) {
  return get(`/documents/${pageNumber ? `?pageNumber=${pageNumber}` : ``}`)
    // eslint-disable-next-line
    .then(response => parser.instantiateSamples(response))
}

export function getDocument (uid) {
  return get('documents/' + uid)
    // eslint-disable-next-line
    .then(response => parser.instantiateSamples(response))
}

export function saveState (samples) {
  let preparedSamples = parser.prepareSamplesForPOST(samples)
  axios.post('/save', preparedSamples)
}

export function updateState (samples) {
  let preparedSamples = parser.prepareSamplesForPOST(samples)
  axios.post('/update', preparedSamples)
}



