import * as texoo from '@/api/texoo-datamodel'
import * as sessionModel from '@/api/session-datamodel'

export function instantiateSamples (response) {
  let samples = []

  for (let sample of response.data) {
    let classs = sample.class
    let begin = sample.begin
    let length = sample.length
    let text = sample.text
    let annotations = instantiateRelations(sample.annotations)
    let uid = sample.uid
    let documentRef = sample.documentRef
    let type
    let id
    let language
    let tokens
    let empty
    let sentences
    let source
    let finished = false
    let highlightedText
    let title = sample.title

    samples.push(new texoo.Sample(length, documentRef, uid, text, begin, classs, tokens, empty, type, annotations, language, sentences, source, id, finished, highlightedText, title))
  }
  return samples
}

export function instantiateRelations (response) {
  let relations = []

  for (let relation of response) {
    let length = relation.length
    let documentRef = relation.documentRef
    let uid = relation.uid
    let text = relation.text
    let begin = relation.begin
    let classs = relation.classs
    let type = relation.type
    let source = relation.source
    let confidence = relation.confidence
    let isActive = relation.isActive
    let predicate = relation.predicate
    let relationArguments = instantiateAnnotations(relation.relationArguments)
    relations.push(new texoo.Relation(length, documentRef, uid, text, begin, classs, type, source, confidence, isActive, predicate, relationArguments))
  }
  return relations
}

export function instantiateAnnotations (response) {
  let annotations = []

  for (let annotation of response) {
    let classs = annotation.class
    let begin = annotation.begin
    let text = annotation.text
    let source = annotation.source
    let confidence = annotation.confidence
    let type = annotation.type
    let length = annotation.length
    let uid = null
    let documentRef = null
    let isActive = false
    annotations.push(new texoo.Annotation(length, documentRef, uid, text, begin, classs, type, source, confidence, isActive))
  }
  return annotations
}

export function instantiateSessions (response) {
  let sessions = []
  for (let session of response.data) {
    let uid = session.uid
    let f1Score = session.f1Score
    sessions.push(new sessionModel.Session(uid, f1Score))
  }
  return sessions
}

export function prepareSamplesForPOST (samples) { // remove the leading underscores
  let preparedSamples = []

  for (let sample of samples) {
    let preparedSample = {}
    for (let key in sample) {
      let keyWithoutUnderscore = key.replace('_', '')
      let value = keyWithoutUnderscore === 'annotations' ? prepareAnnotationsForPOST(sample.annotations) : sample[keyWithoutUnderscore]

      preparedSample[keyWithoutUnderscore] = value // add propertie with value to preparedSample
    }
    preparedSamples.push(preparedSample)
  }
  return preparedSamples
}

export function prepareAnnotationsForPOST (annotations) {
  let preparedAnnotations = []

  for (let annotation of annotations) {
    let preparedannotation = {}
    for (let key in annotation) {
      let keyWithoutUnderscore = key.replace('_', '')
      let value = annotation[keyWithoutUnderscore]      // to invoke the getter
      preparedannotation[keyWithoutUnderscore] = value  // add propertie with value to preparedannotation
    }
    preparedAnnotations.push(preparedannotation)
  }

  return preparedAnnotations
}



